import { div, input, } from "@cycle/dom";

// intent (events => actions)
function intent(domSource) {
  return {
    changeValue$: domSource
      .select(".slider")
      .events("input")
      .map(ev => ev.target.value)
  };
}

// model (actions => state)
function model(actions, props$) {
  const state$ = props$
    .map(
      props =>
        // 
        actions.changeValue$
          .map(value => ({
            label: props.label,
            unit: props.unit,
            min: props.min,
            value,
            max: props.max
          }))
          .startWith(props) // 
    )
    .flatten() // (props$)(changeValue$state$)(changeValue$),flatten()
    .remember(); // .startWith()of\creat\combine

  return state$;
}

// view (state => dom)
function view(state$) {
  return state$.map(state =>
    div({ style: { marginBottom: "10px" } }, [
      `${state.label}: ${state.value} ${state.unit}`,
      input(".slider", {
        style: { marginLeft: "10px", display: "inline-block" },
        attrs: {
          type: "range",
          min: state.min,
          max: state.max,
          value: state.value
        }
      })
    ])
  );
}

function LabeledSlider(sources) {
  const actions = intent(sources.DOM);
  const state$ = model(actions, sources.props);
  const vdom$ = view(state$);

  const sinks = {
    DOM: vdom$,
    value: state$.map(state => state.value)
  };

  return sinks;
}

export default LabeledSlider;



